.header {
  background-color: #131921;
  display: flex;
  align-items: center;
  position:sticky;
  top:0;
  z-index: 100;
}
.header__nav {
  display: flex;
  justify-content: space-evenly;
}
.header__logo {
  width: 100px;
  margin: 0 20px;
  margin-top: 10px;
}
.header__option {
  display: flex;
  flex-direction: column;
  margin-left: 10px;
  margin-right: 10px;
}
.header__option1 {
  font-size: 10px;
}
.header__option2 {
  font-size: 13px;
  font-weight: 800;
}
.header__link {
  text-decoration: none;
  color: white;
}
.header__search {
  display: flex;
  flex: 1;
}
.header__searchInput {
  height: 12px;
  padding: 10px;
  border: none;
  width: 100%;
}
.header__searchIcon {
  background-color: #cd9042;
  padding: 5px;
  height: 22px !important;
}
.header__optionbasket {
  display: flex;
  align-items: center;
}
.header__basketcount {
  margin-left: 10px;
  margin-right: 10px;
  align-items: center;
}
