.product {
  display: flex;
  flex-direction: column;
  justify-content: end;
  margin:10px ;
  padding: 20px;
  width: 100%;
  max-height: 400px;
  min-width: 100px;
  background-color: white;
  z-index: 1;
}
.product__rating {
  display: flex;
}
.product > img {
  object-fit: contain;
  width: 100%;
  max-height: 200px;
}
.product__price {
  margin-top: 5px;
}
.product__info {
  height: 100px;
  margin-bottom: 15px;
}
.product > button {
  background-color: #f0c14b;
  border: 1px solid;
  border-color: #a88734 #9c7e31 #846a29;
  width: 190px;
  margin-top: 15px;
  align-self: center;
}
